import React, { useState, useReducer, useEffect } from "react";
import FlipCard from "./FlipCard";
import styled, { css } from "styled-components";
import Reducer from "./Reducer";
import { Actions, ActionKeys } from "./interfaces";
import { ActionSheetIOS } from "react-native";
const MainContainer = styled.main`
  border: 0px solid red;
  color: white;
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-areas:
    "header"
    "items"
    "footer";
  height: 100vh;
`;

const Header = styled.header`
  grid-area: header;
  padding: 0.8em;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  font-size: 1.2em;
  box-shadow: 0 1px 30px -5px rgba(0, 0, 0, 0.3);
`;

const Footer = styled.footer`
  grid-area: footer;
  padding: 0.8em;
  height: 1em;
  box-shadow: 1px 0px 30px -5px rgba(0, 0, 0, 0.3);
`;

const ItemsGrid = styled.section`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 0.5em;
  grid-area: items;
  justify-items: center;
  overflow: auto;
  padding: 1em;
`;

const App = () => {
  const [state, dispatch] = useReducer(Reducer, {
    cards: [],
    groups: [{ id: "1", name: "Russian Verbs" }]
  });

  const { cards } = state;

  const [isFlipped, setIsFlipped] = useState(false);
  const handlePlayAudio = (file: string) => {
    try {
      const aud = new Audio();

      aud.src = `./audio/${file}`;
      aud.load();
      aud.play();
    } catch (error) {
      console.dir(JSON.stringify(error));
    }
  };

  useEffect(() => {
    dispatch({
      type: ActionKeys.CARDS_LOAD,
      cards: [
        {
          id: "1",
          title: "",
          imageLink: "https://media.giphy.com/media/stv1Dliu5TrMs/giphy.gif",
          type: "text",
          groupId: "1",
          answer: "работа",
          pronunciation: 'rabota',
          audioAnswer: "job.mp3",
          answerRevealed: false
        }
      ]
    });
  }, []);

  // console.dir(state);

  const handleFlipping = () => setIsFlipped(x => !x);

  return (
    <MainContainer>
      <Header>
        <span style={{ fontStyle: "italic" }}>Flash</span>
        <span style={{ fontWeight: "bold" }}>Cards</span>
      </Header>
      <ItemsGrid>
        {cards.map(x => (
          <FlipCard
            key={x.id}
            front={
              <div style={{ padding: "1em" }}>
                <img width={250} src={x.imageLink} />
              </div>
            }
            back={
              <div>
                <div>{x.answer}</div>
                <div>{x.pronunciation}</div>
                <button onClick={() => handlePlayAudio(x.audioAnswer)}>
                  Play
                </button>
              </div>
            }
            isFlipped={isFlipped}
            handleFlipping={handleFlipping}
          />
        ))}
      </ItemsGrid>
      <Footer />
    </MainContainer>
  );
};

export default App;
