import { AppState, Actions, ActionKeys } from "../interfaces";

export const Reducer = (
  state: AppState = { cards: [], groups: [] },
  actions: Actions
): AppState => {
  switch (actions.type) {
    case ActionKeys.CARDS_LOAD:
      return { ...state, cards: actions.cards };

    default:
      return state;
  }
};

export default Reducer;
