export interface LanguageQuestion {
  id: string;
  title: string;
  imageLink: string;
  proposed?: string;
  answer: string;
  pronunciation?: string;
  audioAnswer: string;
  type: "audio" | "text";
  groupId: string;
  group?: Group;
  answerRevealed: boolean;
}

export interface Group {
  id: string;
  name: string;
}

export interface AppState {
  cards: LanguageQuestion[];
  currentGroupId?: string;
  groups: Group[];
}

export enum ActionKeys {
  CARDS_LOAD = "[cards] Load",
  CARD_FLIP = "[card] Flip"
}

export type Actions = LoadCardsAction | FlipCardAction;

interface LoadCardsAction {
  type: ActionKeys.CARDS_LOAD;
  cards: LanguageQuestion[];
}

interface FlipCardAction {
  type: ActionKeys.CARD_FLIP;
  id: string;
}
